import { useHttp } from "../../hooks/http.hook";
import { useDispatch, useSelector } from "react-redux";

import { heroCreating, heroCreated, heroCreatingError } from "../../actions";

import { v4 as uuidv4 } from "uuid";
import {
  Formik,
  Form,
  Field,
  ErrorMessage as FormikErrorMessage,
} from "formik";
import * as Yup from "yup";

import Spinner from "../spinner/Spinner";

const HeroesAddForm = () => {
  const { filtersArr, loadingStatus: filtersLoadingStatus } = useSelector(
    (state) => state.filters
  );
  const dispatch = useDispatch();
  const { request } = useHttp();

  return (
    <Formik
      initialValues={{ name: "", text: "", element: "  ..." }}
      validationSchema={Yup.object({
        name: Yup.string()
          .min(3, "    3 ")
          .max(16, "    16 ")
          .required(" "),
        text: Yup.string()
          .min(3, "    3 ")
          .max(48, "    48 ")
          .required(" "),
        element: Yup.string().oneOf(
          ["fire", "water", "wind", "earth"],
          "   "
        ),
      })}
      onSubmit={({ name, text, element }, { resetForm }) => {
        const newHero = {
          id: uuidv4(),
          name: name,
          description: text,
          element: element,
        };
        dispatch(heroCreating());
        request(`http://localhost:3001/heroes`, "POST", JSON.stringify(newHero))
          .then(() => {
            resetForm();
            dispatch(heroCreated(newHero));
          })
          .catch(() => dispatch(heroCreatingError()));
      }}
    >
      <Form className="border p-4 shadow-lg rounded">
        <div className="mb-3">
          <label htmlFor="name" className="form-label fs-4">
              
          </label>
          <Field
            required
            type="text"
            name="name"
            className="form-control"
            id="name"
            placeholder="  ?"
          />
          <FormikErrorMessage
            component="div"
            name="name"
            style={{ color: "red" }}
          />
        </div>

        <div className="mb-3">
          <label htmlFor="text" className="form-label fs-4">
            
          </label>
          <Field
            as="textarea"
            required
            name="text"
            className="form-control"
            id="text"
            placeholder="  ?"
            style={{ height: "130px" }}
          />
          <FormikErrorMessage
            component="div"
            name="text"
            style={{ color: "red" }}
          />
        </div>

        <div className="mb-3">
          {filtersLoadingStatus === "fetching" ? (
            <Spinner />
          ) : (
            <>
              <label htmlFor="element" className="form-label">
                  
              </label>
              <Field
                as="select"
                required
                className="form-select"
                id="element"
                name="element"
                disabled={filtersLoadingStatus === "fetch-error"}
              >
                <option>  ...</option>
                {filtersArr
                  .filter((item) => item.name !== "all")
                  .map((item) => (
                    <option key={item.id} value={item.name}>
                      {item.label}
                    </option>
                  ))}
              </Field>
            </>
          )}
          <FormikErrorMessage
            component="div"
            name="element"
            style={{ color: "red" }}
          />
        </div>

        <button type="submit" className="btn btn-primary">
          
        </button>
      </Form>
    </Formik>
  );
};

export default HeroesAddForm;
